import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  appUrlSelector,
  headerLinksSelector,
  supportNumberSelector,
} from 'src/services/options/selectors';
import { connect } from 'react-redux';
import { Logo } from 'src/components/Logo';
import SideNav from 'src/components/SideNav';
import {
  Chat,
  ChatBtn,
  ChatText,
  HeaderContent,
  HeaderContentTop,
  HeaderLink,
  HeaderLinks,
  HeaderWrap,
  PopupBtn,
  PopupBtnIcon,
  PopupBtnText,
  PopupBtnWrap,
  PopupContent,
  RoundBtn,
} from 'src/components/Header/styled';
import { contactsContentSelector } from 'src/services/pages/selectors';
import { SideNavBtn } from 'src/components/SideNav/styled';
import { IconMenu } from 'src/components/Icons/IconMenu';
import { getOptionProps, parseMarkdownToHtml, toggleChat } from 'src/utils/utils';


class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isContactsOpen: false,
      isSideNavOpen: false,
    };
  }

  toggleSideNav = () => {
    this.setState(state => ({ isSideNavOpen: !state.isSideNavOpen }));
  };

  toggleContactsPopup = () => {
    this.setState(state => ({
      isContactsOpen: !state.isContactsOpen,
    }));
  };

  renderContactLink = () => {
    const { links, contactsContent, supportNumber } = this.props;
    const { isContactsOpen } = this.state;

    const link = links.find(l => l.name === 'Contacts');
    if (!link) return <div />;

    const html = parseMarkdownToHtml(contactsContent);
    return (
      <PopupBtnWrap>
        <PopupBtnText>{supportNumber}</PopupBtnText>
        <PopupBtn>
          <PopupBtnIcon
            onClick={this.toggleContactsPopup}
            direction={isContactsOpen ? 'up' : 'down'}
          />
          <Link to={link.target}>{link.label}</Link>
          <PopupContent
            visible={isContactsOpen}
            dangerouslySetInnerHTML={{ __html: html }}
          />
        </PopupBtn>
      </PopupBtnWrap>
    );
  };

  renderMainLinks = () => {
    const { links } = this.props;

    return links
      .filter(link => link.name !== 'Contacts')
      .map((link) => {
        const props = getOptionProps(link);
        const isMobileVisible = link.name === 'Faq' || link.name === 'AboutUs';

        return (
          <HeaderLink
            className={isMobileVisible ? '' : 'hidden-tablet-down'}
            {...props}
          >
            {link.label}
          </HeaderLink>
        );
      });
  };

  render() {
    const { appUrl } = this.props;
    const { isSideNavOpen } = this.state;

    return (
      <HeaderWrap id="header">
        <SideNav isOpen={isSideNavOpen} toggleSideNav={this.toggleSideNav} />

        <HeaderContentTop>

          <SideNavBtn as={IconMenu} onClick={this.toggleSideNav} className="hidden-tablet-up" />
          <Logo to="/" className="hidden-tablet-up" />
          <RoundBtn href={appUrl} target="_blank" className="hidden-tablet-up">GET APP</RoundBtn>


          <HeaderLinks className="visible-tablet-up">
            {this.renderMainLinks()}
          </HeaderLinks>

          <Chat onClick={toggleChat} className="visible-tablet-up">
            <svg height="34px" viewBox="-21 -47 682.66669 682" width="34px" xmlns="http://www.w3.org/2000/svg">
              <path d="m552.011719-1.332031h-464.023438c-48.515625 0-87.988281 39.464843-87.988281 87.988281v283.972656c0 48.414063 39.300781 87.816406 87.675781 87.988282v128.863281l185.191407-128.863281h279.144531c48.515625 0 87.988281-39.472657 87.988281-87.988282v-283.972656c0-48.523438-39.472656-87.988281-87.988281-87.988281zm50.488281 371.960937c0 27.835938-22.648438 50.488282-50.488281 50.488282h-290.910157l-135.925781 94.585937v-94.585937h-37.1875c-27.839843 0-50.488281-22.652344-50.488281-50.488282v-283.972656c0-27.84375 22.648438-50.488281 50.488281-50.488281h464.023438c27.839843 0 50.488281 22.644531 50.488281 50.488281zm0 0" />
              <path d="m171.292969 131.171875h297.414062v37.5h-297.414062zm0 0" />
              <path d="m171.292969 211.171875h297.414062v37.5h-297.414062zm0 0" />
              <path d="m171.292969 291.171875h297.414062v37.5h-297.414062zm0 0" />
            </svg>
            <ChatText>Chat online</ChatText>
          </Chat>
          <div className="visible-tablet-up">
            {this.renderContactLink()}
          </div>
          <RoundBtn href={appUrl} target="_blank" className="visible-tablet-up">GET APP</RoundBtn>

        </HeaderContentTop>

        <HeaderContent className="hidden-tablet-up">
          <HeaderLinks className="hidden-xs-down">
            {this.renderMainLinks()}
          </HeaderLinks>

          {this.renderContactLink()}
          <ChatBtn onClick={toggleChat} />
        </HeaderContent>

      </HeaderWrap>
    );
  }
}

Header.propTypes = {
  links: PropTypes.arrayOf(PropTypes.object).isRequired,
  contactsContent: PropTypes.string.isRequired,
  supportNumber: PropTypes.string.isRequired,
  appUrl: PropTypes.string.isRequired,
};

const mapStateToProps = state => ({
  links: headerLinksSelector(state),
  contactsContent: contactsContentSelector(state),
  supportNumber: supportNumberSelector(state),
  appUrl: appUrlSelector(state),
});

export default connect(mapStateToProps)(Header);
