import showdown from 'showdown';
import { Link } from 'react-router-dom';


/**
 * Creates an object with props for a react component.
 * 'option' component may be: react-router 'Link', plain 'a'
 * @param {Object} option - Option object from API response(options)
 * @returns {Object}
 */
export const getOptionProps = (option) => {
  const props = { key: option.id };

  // local links
  if (option.target) {
    props.as = Link;
    props.to = option.target;
    // external links
  } else if (option.url) {
    props.href = option.url;
    props.target = '_blank';
    props.rel = 'noopener noreferrer';
  } else {
    console.warn(`Option(id: ${option.id}) doesn't have required fields.`);
  }

  return props;
};

export const parseMarkdownToHtml = (markdown) => {
  const converter = new showdown.Converter();
  return converter.makeHtml(markdown);
};

/**
 *
 * @param {HTMLElement} form
 * @returns {string}
 */
export const serializeForm = (method, form) => {
  const data = [];

  for (let i = 0; i < form.elements.length; i += 1) {
    const field = form.elements[i];

    // Some validation
    if (!field.name || field.disabled || field.type === 'file' || field.type === 'reset' || field.type === 'submit' || field.type === 'button') continue;

    // if (field.type === 'select-multiple') {
    //   for (let n = 0; n < field.options.length; n += 1) {
    //     if (field.options[n].selected) {
    //       data.push(`${encodeURIComponent(field.name)}=${encodeURIComponent(field.options[n].value)}`);
    //     }
    //   }
    // }
    if ((field.type !== 'checkbox' && field.type !== 'radio') || field.checked) {
      if (method === 'GET') {
        data.push(`${encodeURIComponent(field.name)}=${encodeURIComponent(field.value)}`);
      } else if (method === 'POST') {
        data.push({ [field.name]: [field.value] });
      }
    }
  }

  if (method === 'GET') return data.join('&');
  if (method === 'POST') return data;
};

/**
 * Show/hide chat widget
 */
export const toggleChat = () => {
  // eslint-disable-next-line camelcase
  if (Tawk_API) Tawk_API.toggle();
};

export const runGoogleAnalytics = () => {
  window.dataLayer = window.dataLayer || [];
  function gtag(...args) { window.dataLayer.push(args); }
  gtag('js', new Date());
  gtag('config', 'UA-38853360-1');
};
